.app-wrapper{
margin: 0 auto;
width: 1200px;
display: grid;
grid-template-areas: 
"h h"
"n c";
grid-template-rows: 60px 1fr;
grid-template-columns: 2fr 10fr;
}
.app-wrapper-content{
  display: flex;
  justify-content: center;
  align-items: center;
grid-area: c;
background-color: cornflowerblue;
grid-template-areas: 
  "a b a";
  grid-template-columns: 30px 300px 30px;
}
  
